body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
}
.header{
    background-image: url(/Clicky-Game/static/media/footer_lodyas.9a30e6c6.png);
    text-align: center;
    color: white;
    padding: 175px 0px;
}
.navbar{
    background-color: darkblue;
    color: white;
    min-height: 70px;
    position: fixed;
    top:0;
    width: 100%;
    font-size: 20px;
    box-shadow: 0 3px 6px black, 0 3px 6px #999;

}

ul {
    list-style-type: none;
    display: inline-block;
    margin: 0;
    padding: 0;
    overflow: hidden;
    width: 95%;
    padding: 14px 16px;
}
li {
    float: left;
    text-align: center;
    width: 33%;
}

a {
    color: white;
    text-decoration: none;
    font-weight: bolder;
}


.wrapper {
  background: #ECEFF1;
  height: 100%;
  width: 100%;
  border: 0;
  margin: 0;
  padding: 0;
  overflow: auto;
}

.img-container {
    background: #fff;
    border-radius: 2px;
    border: 5px solid #f1f1f1;
    height: 175px;
    margin: 15px;
    border: 10px;
    width: 175px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    float: left;
  }

  .click-item{
    padding: 5px;
    width: 100%;
    height: 100%;

  }

  .click-item:hover {
    -webkit-transform: scale(1.05);
    transform: scale(1.05);
    box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22);
    -webkit-transition: .1s;
    transition: .1s;
  }
.footer{
    background-color: darkblue;
    color: white;
    position: absolute 0;
    font-size: 18px;
    text-align: center;
    padding: 18px;
}

#logo{
    height: 28px;
    width: 36px;
}
.container{
    overflow: auto;
    width: 75%;
    margin: auto;
}
